"use client"

import { InputAdornment, InputLabel, TextField } from "@mui/material";
import { Controller, UseFormReturn } from "react-hook-form";


//Enter the placeholder or inputTitle for the input description 
interface CustomTextFieldProps {
    placeholder?: string;
    size?: "medium" | "small"; // medium is default
    variant?: "outlined" | "filled" | "standard"; //outlined is default
    label?: string;
    disabled?: boolean
    IconComponent?: React.ElementType;
    iconPosition?: "start" | "end";
    fullWidth: boolean;
    borderColor?: string; // border color
    textColor?: string;
    bgColor?: string;
    form: UseFormReturn<any>
    inputTitle?: string;
    formFieldName: string; // name of formValue
    useLabel: boolean; 
}

export default function CustomTextField(
    {
        placeholder,
        size,
        variant,
        label,
        IconComponent,
        iconPosition,
        fullWidth,
        borderColor,
        textColor,
        bgColor,
        form,
        inputTitle,
        formFieldName,
        useLabel
    }
        : CustomTextFieldProps) {
    return (
        <>
            
            <Controller
                name={formFieldName}
                control={form.control}
                render={({ field, fieldState: { error } }) => (
                    <>
                    {inputTitle && (<InputLabel>{inputTitle}</InputLabel>)}
                    <TextField
                        {...field}
                        {...(useLabel && { label: label })}
                        placeholder={placeholder}
                        variant={variant || "outlined"}
                        size={size || "medium"}
                        fullWidth={fullWidth}
                        error={!!error}
                        helperText={error?.message}
                        slotProps={{
                            input: {
                                ...(IconComponent && iconPosition === "start" && {
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <IconComponent />
                                        </InputAdornment>
                                    )
                                }),
                                ...(IconComponent && iconPosition === "end" && {
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            <IconComponent />
                                        </InputAdornment>
                                    )
                                }),
                                style: {
                                    color: textColor || "dark",
                                    backgroundColor: bgColor || "white",
                                }
                            },
                            htmlInput: {
                                style: {
                                    color: textColor || "dark",
                                }
                            }
                        }}
                        sx={{
                            '& .MuiOutlinedInput-root': {
                                '& fieldset': {
                                    borderColor: borderColor || "grey",
                                },
                                '&:hover fieldset': {
                                    borderColor: borderColor || "grey",
                                },
                                '&.Mui-focused fieldset': {
                                    borderColor: borderColor || "grey",
                                },
                                
                            },
                        }}
                    />
                    </>
                )}
            />
        </>
    )
}